
* {
  margin: 3px 0;
  font-weight: 300;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-color: #FFF;
  background-size: cover;
  background-position: center;
  margin: 5em 5%;

  border-style: solid;
  border-width: 1px;
}
body {
  background: url(assets/blur2.jpg) no-repeat center center fixed;
  background-size: cover;
  background-position: center;
  height: 100vh;
}
h1, h2, h3, h4 {
  font-weight: 300;
}
h1 {
  font-size: 2em;
}
a {
  color: inherit;
  font-weight: 500;
  text-decoration: none;
}



